@charset "UTF-8";

@import 'partials/_base';

.g-container {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 100vh;
	
	.g-content.g-intro {
		display: flex;

		.g-intro-inner {
			display: flex;
			flex-direction: column;
			row-gap: 64px;

			.g-intro-head {
				display: flex;
				flex-direction: column;
				row-gap: 10px;

				.g-intro-logo {
					max-width: 300px;
					margin: 0 auto;

					svg {
						width: 100%;
					}
				}

				.g-intro-subject {
					font-size: 48px;
					font-weight: 500;
					line-height: 1;
				}
			}

			.g-intro-body {
				.para {
					font-size: 18px;
					font-weight: 500;
					line-height: 1.5;
					color: #c1c1c1;
				}

				.button-area {
					display: flex;
					align-items: center;
					justify-content: center;
					gap: 12px;

					.btn {
						flex: 1 1 100%;
						display: inline-flex;
						align-items: center;
						justify-content: center;
						position: relative;
						z-index: 1;
						height: 52px;
						box-sizing: border-box;
						border-radius: 4px;
						background-color: #c1c1c1;
						font-weight: 500;
						color: #fff;
						text-align: center;
						transition: all 0.3s ease-in-out;

						&:hover {
							background-color: #a9a9a9;
						}
					}
				}
			}
		}
	}
}
